.modal-content {
    border-radius: 0;
}

.close {
    opacity: 0.4;
    
    &:hover,
    &:focus {
        opacity: 0.7;
    }
}

.modal-dialog-gl {
    width: 530px;

    .modal-header {
        border-bottom: 0;
        padding: 30px 40px 0 40px;
        
        .close {
            position: relative;
            top: -10px;
            right: -20px;
        }
    }
    
    .modal-title {
        font-weight: 600;
    }

    .modal-body {
        padding: 20px 40px 40px 40px;
    }
}

@media (max-width: 767px) {
    .modal-dialog-gl {
        margin: 30px auto;
    }
}

@media (max-width: 600px) {
    .modal-dialog-gl {
        width: auto !important;
        margin: 0;
        
        .modal-header {
            padding: 20px 15px 0 15px;
            
            .close {
                right: 0px;
            }
        }
        
        .modal-content {
            border-width: 0px;
        }
        
        .modal-body {
            padding: 20px 15px 40px 15px;
        }
    }
    

}