body {
    font-family: "Open Sans", sans-serif;
    min-width: 320px;
    padding-top: 70px;
    color: #000;
}

.content {
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 60px;
}

@media (min-width: 1366px) {
    .navbar {
        .container-fluid {
            max-width: 1280px;
            padding-left: 0;
            padding-right: 0;
        }
    }

    .content {
        max-width: 1280px;
        margin-left: auto;
        margin-right: auto;
    }
}

hr {
    margin-top: 25px;
    margin-bottom: 25px;
}

h1,
h2,
h3 {
    color: #000;
}

h1 {
    font-size: 20px;
    font-weight: 600;
    margin-top: 20px;
    margin-bottom: 15px;
}

h2 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 20px;
    margin-top: 15px;
}

.head-with-btn {
    margin-top: 20px;
    margin-bottom: 30px;

    h1 {
        margin-top: 7px;
        margin-bottom: 0;
    }

    .btn {
        float: right;
    }
    
    &.resp {
        h1 {
            float: left;
        }
    }
}

@media (max-width: 767px) {
    .head-with-btn {
        margin-top: 0px;
        margin-bottom: 20px;

        &.resp {
            h1,
            .btn {
                float: none;
            }
            
            h1 {
                margin-top: 0;
            }
            
            .btn {
                margin-top: 10px;
                width: 100%;
            }            
        }
    }
}

.nav-tabs {
    margin-top: 30px;
    margin-bottom: 30px;
}

.btn-group-radio {
    margin-bottom: 10px;    
}

.not-set {
    color: #CC5555;
    font-style: italic;
}

.datepicker input{
   font-size: 13px;
}

// media
@media (max-width: 991px) {
    body {
        min-height: 500px;
    }
    
    .content {
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 30px;
    }
    
    h1 {
        margin-top: 10px;
        margin-bottom: 10px;
    }
}

@media (max-width: 767px) {
    h1,
    h2 {
        font-size: 16px;
        line-height: 1.42857143;
    }
    
    h1 {
        color: #333;
        margin-top: 5px;
        margin-bottom: 10px;
    }
}

.pagination-container {
    text-align: right;
}

@media screen and (max-width: 767px) {
    .pagination-container {
        text-align: center;
    }
    
    .pagination > li > a,
    .pagination > li > span {
      padding: 5px 10px;
      font-size: 12px;
      line-height: 1.5;
    }
    .pagination > li:first-child > a,
    .pagination > li:first-child > span {
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px;
    }
    .pagination > li:last-child > a,
    .pagination > li:last-child > span {
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
    }    
}

