.panel-action-feedback {
    white-space: nowrap;
    
    .btn {
        background-color: transparent;
        
        > span {
            margin-left: 2px;
        }
        
        &.btn-link {
            &:hover,
            &:focus {
                text-decoration: none;
            }
        }

        &.disabled {
            color: #ccc;
        }
    }
    
    .fa {
        font-size: 14px;
        margin-right: 5px;
    }
    
    .like-yes {
        color: #38a138;
    }
    
    .like-no {
        color: #ff0000;
    }    
}

.status-read {
    padding: 5px 10px;
    white-space: nowrap;
    
    &.status-read-yes {
        background-color: #e1f6cc;
    }
    
    &.status-read-no {
        background-color: #ffd4d4;
    }
}

